ul {
  list-style-type: none;
  /* width: 100%; */
  padding: 10px;
  height: 70px;
  background-color: rgba(245, 250, 250, 0.986);
  box-shadow: 1rem 2rem 5rem white;
  margin-bottom: 20px;
}

li {
 float:left
}


li a {
  display: block;
  color: rgb(44, 12, 12);
  text-align: center;
  padding: 10px 14px;
  text-decoration: none;
  font-size: 2rem;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

li a:hover {
  background-color: rgb(3, 116, 145);
  color: white;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}
.dropdown:hover .dropdown-content {
  display: block;
}



.column {

    float: left;
    width: 25%;
    padding: 0 10px;
  
}

.row {margin: 300px -5px;}

.row:after {
  content: "";
  display: table;
  clear: both;
}
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}
header{
  background-color: rgb(58, 59, 59);
  height: 100px;
  position: relative;
}
